@media (max-width: 991px) {
  .nav-link {
    display: none;
  }
  .nav-link-column {
    justify-content: flex-end;
    align-items: center;
  }
  .video-row {
    height: auto;
    padding-bottom: 10px;
  }
  .heading.control {
    font-size: 24px;
  }
  .video-section {
    height: auto;
  }
  .video-col {
    height: auto;
  }
  .icon {
    color: #ffcd00;
  }
  .dropdown {
    display: block;
  }
  .image-5 {
    height: 25px;
  }
  .div-block-9 {
    display: block;
  }
  .video-container {
    width: 95%;
  }
}

@media (max-width: 767px) {
  .nav-link {
    display: none;
  }
  .nav-link.w--current {
    display: none;
  }
  .nav-link-column {
    height: 100%;
    justify-content: flex-end;
    align-items: center;
  }
  .control-col {
    height: auto;
  }
  .video-col {
    height: auto;
    padding-bottom: 10px;
  }
  .icon {
    color: #ffcd00;
  }
  .dropdown {
    display: block;
    height: 100%;
  }
  .dropdown-toggle {
    display: flex;
    height: 100%;
    justify-content: center;
    align-items: center;
  }
  .image-5 {
    position: relative;
    display: inline-block;
  }
  .section-2 {
    height: auto;
  }
  .columns-4 {
    position: static;
  }
  .div-block-9 {
    display: block;
  }
}

@media (max-width: 479px) {
  .nav-link {
    display: none;
  }
  .nav-link-column {
    justify-content: flex-end;
    align-items: center;
  }
  .video-row {
    height: auto;
  }
  .heading {
    display: block;
  }
  .heading.control {
    display: none;
  }
  .control-box {
    margin-bottom: 10px;
  }
  .button {
    margin-bottom: -1px;
  }
  .video-section {
    height: auto;
  }
  .planeterrella-banner {
    height: 50px;
    box-shadow: 2px 10px 21px 0 #8f8e8e;
  }
  .icon {
    margin-right: 0px;
    color: #ffcd00;
  }
  .dropdown {
    position: relative;
    display: block;
  }
  .nav-link-dropdown {
    display: block;
    flex: 1 1;
    font-size: 12px;
    line-height: 40px;
  }
  .dropdown-toggle {
    display: flex;
    height: 25px;
    padding-right: 20px;
    justify-content: center;
    align-items: center;
  }
  .dropdown-list {
    position: absolute;
    top: 45px;
    right: -12px;
    bottom: 0px;
    display: none;
    width: 50vw;
    height: 37vh;
    padding: 10px;
    justify-content: flex-end;
    background-image: linear-gradient(180deg, #363636, #363636);
    text-align: right;
  }
  .section-2 {
    height: auto;
  }
  .heading-2 {
    display: none;
  }
  .heading-2.info {
    display: block;
  }
  .footer-info {
